body {
  margin: 20px;
  width: 1400px;
  font-size: 12pt !important;
  color: #373D3F;
}

input[type="text"] {
  border: 1px solid #ccc;
  padding: 5px;
  border-radius: 3px;
}

section {
  margin-top: 20px;
  position: relative;
}

aside {
  float: left;
  width: 300px;
}

aside input[type="text"] {
  width: 100%;
  margin-bottom: 5px;
}

article {
  float: left;
  margin: 50px;
  margin-top: -5px;
  max-width: 1000px;
}

article textarea {
  width: 400px;
  height: 150px;
  border-radius: 3px;
  border: 1px solid #999;
}

article label {
  width: 150px;
  display: inline-block;
  margin: 0;
  vertical-align: top;
}

.layer-menu-label-checkbox {
  width: 300px;
}

article input[type="text"] {
    width: 400px;
  margin-right: 5px;
  margin-bottom: 5px;
}

form > div,
article fieldset > div,
article fieldset > span > div {
  margin: 7px;
}

.btn {
  position: relative;
  top: -2px;
}

.btn-default {
  color: #333;
  background-color: white;
  border-color: #ccc;
}

.btn-default:hover {  
  background-color: #e6e6e6;
  border-color: #adadad;
}

.details {
  float: left;
  width: 310px;
}

.details .fa-times {
  cursor: pointer;
}

.layer-list-choosen {
  border: 1px solid #999;
  border-radius: 3px;
  display: inline-block;
  width: 500px;
  min-height: 40px;
  max-height: 350px;
  overflow: auto;
  cursor: pointer;
}

.layer-list-choosen ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.layer-list-choosen li {
  padding: 5px;
  margin: 5px;
}

.layer-list-choosen .layer {
  display: inline-block;
  padding: 4px;
  border-radius: 3px;
  background: #5cb85c;
  color: white;
  margin-right: 4px;
  margin-bottom: 4px;
}

.layer-list {
  border: 1px solid #999;
  border-radius: 3px;
  display: inline-block;
  width: 500px;
  min-height: 150px;
  max-height: 350px;
  overflow: auto;
  padding: 5px;
  cursor: pointer;
}
.layer-list-test {
  display: block;
