    border: 1px solid #ccc;
    border-radius: 4px;
  overflow: auto;
  cursor: pointer;
  min-height: 250px;
  max-height: 350px;
  margin-left: 15px;
}

.label-block {
  display: block;
}

.no-padding {
  padding: 0 !important;
}

.no-margin-top {
  margin-top: 0;
}

.label-margin {
  margin-left: 15px;
}

.layer-list label {
  width: auto !important;
}

.layer-list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.layer-list li {
  padding: 5px;
}

.layer-list li:hover {
  background: #dedede;
}

.layer-list li:nth-child(even)  {

}

.layer-list li:nth-child(odd)  {

}

.layer-list li i {
  float: right;
  margin-top: 1px;
  padding: 4px;
  border: 1px solid rgba(0,0,0,0);
}

.layer-list li i.active {
    color: #a94442;
    border: 1px solid;
}

.config-layer-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.config-layer-list li {
  border: 1px solid #ccc;
  background: #f8f8f8;
  margin-bottom: 5px;
  padding: 10px;
  width: 100%;
  cursor: pointer;
}

.config-layer-list li i {
  float: right;
  cursor: pointer;
  font-size: 20px;
}

table {

}

table th {
  text-align: center;
  padding: 4px;
  border: 1px solid;
}

table td {
  padding: 4px;
  text-align: left;
  border: 1px solid;
}

.validation-error {
  background: #d9534f;
}

.modal {
  display: inherit !important;
}

body.dragging, body.dragging * {
  cursor: move !important;
}

.dragged {
  position: absolute;
  opacity: 1;
  z-index: 2000;
}

.tree-view > ul li.placeholder {
  position: relative;
  margin: 0;
  padding: 0;
  border: none;
}

.tree-view > ul li.placeholder:before {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  margin-top: -3px;
  left: -3px;
  top: -2px;
  border: 5px solid transparent;
  border-left-color: black;
  border-right: none;
}

.tree-view {
  float: left;
  margin-left: 20px;
  width: auto !important;
}

.tree-view ul {
  list-style: none;
  padding-left: 15px;
}

.tree-view ul li {
  min-width: 300px;
  margin: 3px;
}

.tree-view .layer-node {
  border: 1px solid #ccc;
    background: #f8f8f8;
  margin-bottom: 5px;
  padding: 10px;
  width: 100%;
  cursor: pointer;
}

.tree-view .layer-node.visible {
  font-weight: bold;
}

.tree-view .group-name {
  font-weight: bold;
  cursor: pointer;
}

.nav-tabs {
  overflow: hidden;
}

.info {

}

.tab-pane-bar {
  margin: 0 0 18px 0;
}

.tab-pane aside {
    max-height: 100vh;
    overflow-y: scroll;
}

.expanded-at-start {
  position: relative;
  top: 4px;
}

.expanded-at-start label {
  width: auto;
  margin: 4px;
}

.edit-fields-table {
  display: inline-block;
