}

.edit-fields-table input[type="text"] {
  width: 160px !important;
  margin: 0 !important;
}

.editable-list {
}

.list-value:first-child {
  margin-left: 0 !important;
}

.list-value {
  display: inline-block;
  padding: 4px;
  border-radius: 3px;
  background: #5cb85c;
  color: white;
  margin: 2px;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.geometry-types {
  display: inline-block;
}

.geometry-types label {
  width: auto !important;
}

.color-box {
  width: 100px;
  height: 62px;
  border: 1px solid black;
}

.inset-form {
  padding: 10px;
  background: #f8f8f8;
