}

.article-wrapper {
  width: 580px;
}

.infoclick-heading {
  padding-left: 15px;
}

.infoclick-box {
  border: 1px solid #ccc;
  margin-bottom: 5px;
  border-radius: 4px;
}

.preset-icon {
